import { getDefaultAppState } from "../appState";
import { DEFAULT_EXPORT_PADDING } from "../constants";
import { exportToCanvas } from "../scene/export";

export const getImageFromExcalidraw = async (
  elementsString: string = localStorage.getItem("excalidraw") || "",
) => {
  const elements = JSON.parse(elementsString);
  const appState = getDefaultAppState();
  const canvas = await exportToCanvas(
    elements,
    {
      ...appState,
      offsetLeft: 0,
      offsetTop: 0,
      width: 1024,
      height: 1024,
    },
    {},
    {
      exportBackground: appState.exportBackground,
      viewBackgroundColor: appState.viewBackgroundColor,
      exportPadding: DEFAULT_EXPORT_PADDING,
    },
  );
  return canvas.toDataURL();
};

(window as any).excalidrawZHelper.getImageFromExcalidraw =
  getImageFromExcalidraw;
